import React, { Component } from 'react';
import { NavLink } from 'react-router-dom';
import style from '../css/app.scss';

class App extends Component {
    constructor(props) {
        super(props);
    }
    handleTabEnter (e) {
        console.log('e', e)
    }
    render() {
        return (
            <div className={style.app}>
                {/* <h1 className={style.head}>前端</h1> */}
                <p className={style.desc}>請試著解決以下問題：</p>
                <nav>
                    <NavLink to='/1' className={style.tab} activeClassName={style.active} exact onClick={this.handleTabEnter}>問題 1</NavLink>
                    <NavLink to='/2' className={style.tab} activeClassName={style.active} onClick={this.handleTabEnter}>問題 2</NavLink>
                    <NavLink to='/3' className={style.tab} activeClassName={style.active} onClick={this.handleTabEnter}>問題 3</NavLink>
                    <NavLink to='/4' className={style.tab} activeClassName={style.active} onClick={this.handleTabEnter}>問題 4</NavLink>
                    <NavLink to='/5' className={style.tab} activeClassName={style.active} onClick={this.handleTabEnter}>問題 5</NavLink>
                </nav>
                <main>
                    {this.props.children}
                </main>
            </div>
        )
    }
}

export default App;


