import React, { Component } from 'react';
import style from '../../css/question.scss';

class p3 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3>以F12調整html及css，使以下兩圖片垂直水平置中。</h3>
                <section>
                    <h5>圖片1：</h5>
                    <div>
                        <img src="/images/rx-78-2.jpg" alt="初鋼"/>
                    </div>
                    <h5>圖片2：</h5>
                    <div>
                        <img src="/images/charmander.jpg" alt="小火龍"/>
                    </div>
                    <span className={style.gray}>圖片截自網路。</span>
                </section>
            </article>
        );
    }
}

export default p3;


