import React, { Component } from 'react';
import style from '../../css/question.scss';

class p2 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3>給一正整數列和正整數 k，寫一個函數來檢查數組是否具有至少為 2 的連續子陣列，其總和為 k 的倍數。</h3>
                <section>
                例1：
                <br/>
                　輸入： [23, 2, 4, 6, 7], k = 6
                <br/>
                　輸出： <span className={style.blue}>true</span>
                <br/>
                　說明：因為 [2, 4] 是 length 為 2 的連續子陣列，sum為 6。
                <br/>
                <br/>
                例2：
                <br/>
                　輸入： [23, 2, 6, 4, 7], k = 6
                <br/>
                　輸出： <span className={style.blue}>true</span>
                <br/>
                　說明：因為 [23, 2, 6, 4, 7] 是 length 為 5 的連續子陣列，sum為 42。
                <br/>
                <br/>
                <span className={style.gray}>
                    注意：
                    <br/>
                    <ol>
                        <li>數列的大小不會超過10000。</li>
                        <li>可以假設所有數字的總和在32位整數內。</li>
                    </ol>
                </span>
                </section>
            </article>
        );
    }
}

export default p2;


