import { Route } from 'react-router-dom';
import App from './app.jsx';
import p1 from './views/p1.jsx';
import p2 from './views/p2.jsx';
import p3 from './views/p3.jsx';
import p4 from './views/p4.jsx';
import p5 from './views/p5.jsx';

export default (
    <App>
        <Route exact path="/1" component={p1} />
        <Route path="/2" component={p2} />
        <Route path="/3" component={p3} />
        <Route path="/4" component={p4} />
        <Route path="/5" component={p5} />
    </App>


);


