import React, { Component } from 'react';
import style from '../../css/question.scss';

class p3 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3>給一正整數列，寫一個函數在數列中有任何數字重複時回傳 true，否則回傳 false。</h3>
                <section>
                例1：
                <br/>
                　輸入： [2, 3, 6, 3, 8]
                <br/>
                　輸出： <span className={style.blue}>false</span>
                <br/>
                <br/>
                例2：
                <br/>
                　輸入： [2, 4, 6, 8, 10]
                <br/>
                　輸出： <span className={style.blue}>true</span>
                </section>
            </article>
        );
    }
}

export default p3;


