import React, { Component } from 'react';
import style from '../../css/question.scss';

class p3 extends Component {
    render() {
        return (
            <article className={style.problem}>
                <h3>輸入任意正整數，寫一個函數判斷：
                    <br/>
                    此數為 3 的倍數回傳'皮'，
                    <br/>
                    　　為 5 的倍數回傳'卡'，
                    <br/>
                    　　為 7 的倍數回傳'丘'，
                    <br/>
                    　　為 3、5 之公倍數回傳'皮卡'，
                    <br/>
                    　　為 5、7 之公倍數回傳'卡丘'，
                    <br/>
                    　　為 3、7 之公倍數回傳'皮丘'，
                    <br/>
                    　　為 3、5、7 之公倍數則回傳'皮卡丘'，
                    <br/>
                    其餘皆回傳'小火龍'。</h3>
            </article>
        );
    }
}

export default p3;


